import React, { Component } from "react";
import axios from "axios";
import ReactMarkdown from "react-markdown";

class Post extends Component {
  state = {
    fetchedData: "",
  };

  componentDidMount() {
    axios({
      method: "GET",
      url: `https://saiteja-blog.herokuapp.com/post/${this.props.match.params.id}`,
      headers: { "Content-Type": "application/json" },
    })
      .then((res) => this.setState({ fetchedData: res.data }))
      .then(console.log(this.props.match.params.id));
  }

  render() {
    const { fetchedData } = this.state;
    return (
      <div className="container">
        <div className="row">
          {
            <div>
              <h1 className="mt-5">{fetchedData.title}</h1>
              <ReactMarkdown source={fetchedData.content} />
            </div>
          }
        </div>
      </div>
    );
  }
}

export default Post;
