import React, { Component } from "react";
import TextareaAutosize from "react-textarea-autosize";
import { Button } from "antd";
import { SendOutlined } from "@ant-design/icons";

import axios from "axios";
class AddPost extends Component {
  state = {
    description: "",
    title: "",
    content: "",
    categoryId: "",
    fetchedData: [],
  };
  componentDidMount() {
    axios({
      method: "GET",
      url: "https://saiteja-blog.herokuapp.com/category",
      headers: { "Content-Type": "application/json" },
    }).then((res) => this.setState({ fetchedData: res.data }));
    console.log(this.state.fetchedData);
  }

  onchangeHandler = (e) => {
    e.preventDefault();
    this.setState({ [e.target.name]: e.target.value });
  };
  onSubmit = (e) => {
    e.preventDefault();

    let post;
    post = {
      title: this.state.title,
      content: this.state.content,
      description: this.state.description,
      categoryId: this.state.categoryId,
    };

    axios({
      method: "POST",
      url: "https://saiteja-blog.herokuapp.com/post",
      data: post,
    });
    window.location.href = "/";
  };

  render() {
    const { fetchedData } = this.state;
    return (
      <div className="container">
        <h1>Add New Post</h1>
        <div className="form-group">
          <form onSubmit={this.onSubmit}>
            <label className="text-dark text-blod">Title</label>
            <input
              className="form-control"
              name="title"
              onChange={this.onchangeHandler}
              value={this.state.title}
              placeholder="Title"
            />
            <label className="text-dark text-blod">Description</label>

            <textarea
              className="form-control"
              name="description"
              onChange={this.onchangeHandler}
              value={this.state.description}
              placeholder="Description"
            />
            <label className="text-dark text-blod">Content</label>
            <TextareaAutosize
              className="form-control"
              minRows="18"
              cols="70"
              name="content"
              onChange={this.onchangeHandler}
              value={this.state.content}
              placeholder="Content"
            />
            <label className="text-dark text-blod">Category</label>
            <select
              className="form-control"
              name="categoryId"
              onChange={this.onchangeHandler}
              value={this.state.categoryId}
              placeholder="Category"
            >
              {fetchedData.map((opt) => (
                <option key={opt.id} value={opt.id}>
                  {opt.name}
                </option>
              ))}
            </select>

            <Button type="primary" htmlType="submit" icon={<SendOutlined />}>
              Post
            </Button>
          </form>
        </div>
      </div>
    );
  }
}

export default AddPost;
