import React, { useState } from "react";
import { Link } from "react-router-dom";
const navLinks = [
  {
    title: "Home",
    path: "/home",
  },
  {
    title: "Blog",
    path: "/blog",
  },
  {
    title: "Portfolio",
    path: "/about",
  },
  {
    title: "Contact",
    path: "/contact",
  },
];
export default function Navigation() {
  const [menuActive, setMenuActive] = useState(false);
  return (
    <nav className="site-navigation">
      <span className="menu-title navbrand"><img src ="https://raw.githubusercontent.com/saiteja-konda/Saiteja.dev/master/assets/logos/logo.png" alt="logo" width="50px" height ="50px" /></span>
      <div className={`menu-content-container ${menuActive && `active`} `}>
        <ul>
          {navLinks.map((link, index) => (
            <li key={index}>
              <Link to={link.path}>{link.title}</Link>
            </li>
          ))}
        </ul>
      </div>
      <i
        className="ionicons icon ion-ios-menu"
        onClick={() => setMenuActive(!menuActive)}
      />
    </nav>
  );
}

