import React, { Component } from "react";
import axios from "axios";
import ReactMarkdown from "react-markdown";
import { Link } from "react-router-dom";
class Posts extends Component {
  state = {
    fetchedData: [],
  };
  componentDidMount() {
    axios({
      method: "GET",
      url: "https://saiteja-blog.herokuapp.com/post/",
      headers: { "Content-Type": "application/json" },
    }).then((res) => this.setState({ fetchedData: res.data }));
  }
  handleClick = (e) => {
    e.preventDefault();
    let id = [e.currentTarget.value];
    axios({
      method: "GET",
      url: `https://saiteja-blog.herokuapp.com/post/${id}`,
      headers: { "Content-Type": "application/json" },
    }).then((resp) => console.log(resp));
  };
  render() {
    const { fetchedData } = this.state;

    return (
      <div className="container posts body">
        {fetchedData.map((post) => (
          <div className="box" key={post.id}>
            <div className="content">
              <img src="{https://picsum.photos/800/900}" alt="" />
              <h3>{post.title}</h3>
              <pre>{post.genre}</pre>
              <ReactMarkdown
                source={post.description}
              />
              <Link to={`/post/${post.id}`}>
                <button className="btn btn-sm btn-dark" value={post.id}>
                  Read more
                </button>
              </Link>
            </div>
          </div>
        ))}
      </div>
    );
  }
}

export default Posts;
